'use client';

import React, { useEffect, useState } from 'react';
import { Card } from '../../../components/UI';
import { Users, Building2, TrendingUp, Sparkles, ArrowUpRight, Clock, Zap } from 'lucide-react';
import { useUser } from '@clerk/nextjs';

const StatCard = ({ title, value, change, changeType, icon: Icon, subtext, gradient }: any) => {
  const isPositive = changeType === 'positive';
  const isNegative = changeType === 'negative';

  return (
    <Card className="p-6 hover:shadow-xl transition-all duration-300 border-0 bg-gradient-to-br from-white to-gray-50 dark:from-gray-800 dark:to-gray-900 group">
      <div className="flex justify-between items-start mb-4">
        <div className="flex-1">
          <span className="text-gray-600 dark:text-gray-400 font-medium text-sm uppercase tracking-wide">{title}</span>
        </div>
        <div className={`p-3 rounded-xl bg-gradient-to-br ${gradient} shadow-lg group-hover:scale-110 transition-transform duration-300`}>
          <Icon size={24} className="text-white" />
        </div>
      </div>

      <div>
        <h3 className="text-4xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-gray-900 to-gray-600 dark:from-white dark:to-gray-300 mb-2">
          {value}
        </h3>
        <div className="flex items-center text-sm gap-2">
          {change && (
            <span className={`font-semibold flex items-center gap-1 ${isPositive ? 'text-green-600 dark:text-green-400' :
                isNegative ? 'text-red-600 dark:text-red-400' : 'text-gray-500'
              }`}>
              {isPositive && <ArrowUpRight size={16} />}
              {change}
            </span>
          )}
          <span className="text-gray-500 dark:text-gray-400">{subtext}</span>
        </div>
      </div>
    </Card>
  );
};

interface DashboardStats {
  contacts: number;
  agencies: number;
  usage: {
    count: number;
    total: number;
    remaining: number;
  };
}

export default function Dashboard() {
  const { user } = useUser();
  const [stats, setStats] = useState<DashboardStats | null>(null);
  const [loading, setLoading] = useState(true);
  const [errorMsg, setErrorMsg] = useState<string>('');

  useEffect(() => {
    const loadDashboardData = async () => {
      try {
        const response = await fetch('/api/dashboard/stats');

        if (!response.ok) {
          const text = await response.text();
          console.error('Dashboard stats response error:', response.status, text);
          throw new Error(`Failed to fetch dashboard stats: ${response.status} ${text}`);
        }

        const data: DashboardStats = await response.json();
        setStats(data);
      } catch (error) {
        console.error('Error loading dashboard data:', error);
        setErrorMsg((error as Error)?.message || 'Unknown error');
      } finally {
        setLoading(false);
      }
    };

    loadDashboardData();
  }, []);

  const formatNumber = (num: number) => {
    if (num >= 1000000) return (num / 1000000).toFixed(1) + 'M';
    if (num >= 1000) return (num / 1000).toFixed(1) + 'k';
    return num.toString();
  };

  const usagePercentage = stats ? Math.min(100, Math.round((stats.usage.count / stats.usage.total) * 100)) : 0;
  const isLimitReached = stats && stats.usage.count >= stats.usage.total;

  return (
    <div className="space-y-8">
      {/* Hero Header Section */}
      <div className="relative overflow-hidden rounded-2xl bg-gradient-to-br from-blue-500 via-purple-600 to-blue-700 p-8 shadow-2xl">
        <div className="absolute inset-0 bg-grid-white/10"></div>
        <div className="relative z-10">
          <div className="flex items-center gap-3 mb-3">
            <Sparkles className="text-yellow-300" size={28} />
            <h1 className="text-3xl font-bold text-white">
              Welcome back{user?.firstName ? `, ${user.firstName}` : ''}!
            </h1>
          </div>
          <p className="text-blue-100 text-lg max-w-2xl">
            Here's what's happening with your dashboard today. Track your agencies and manage contacts efficiently.
          </p>
        </div>
        <div className="absolute -bottom-6 -right-6 w-32 h-32 bg-white/10 rounded-full blur-2xl"></div>
        <div className="absolute -top-6 -left-6 w-32 h-32 bg-purple-400/20 rounded-full blur-2xl"></div>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-6">
        <StatCard
          title="Tracked Agencies"
          value={loading || !stats ? "..." : stats.agencies}
          change="+4"
          changeType="positive"
          subtext="new regions"
          icon={Building2}
          gradient="from-orange-400 to-orange-600"
        />
        <StatCard
          title="Total Contacts"
          value={loading || !stats ? "..." : stats.contacts.toLocaleString()}
          change="+150"
          changeType="positive"
          subtext="available leads"
          icon={Users}
          gradient="from-blue-500 to-blue-700"
        />
        <StatCard
          title="Daily API Usage"
          value={loading || !stats ? "..." : `${stats.usage.count}/${stats.usage.total}`}
          change="Resets Daily"
          changeType="neutral"
          subtext="view limit"
          icon={TrendingUp}
          gradient="from-purple-500 to-purple-700"
        />
      </div>

      {errorMsg && (
        <div className="p-4 rounded-xl bg-red-50 dark:bg-red-900/20 border-2 border-red-200 dark:border-red-800 text-sm text-red-700 dark:text-red-400">
          <strong className="font-bold">Error:</strong> {errorMsg}
        </div>
      )}

      {/* Usage Progress Card */}
      <Card className="p-8 border-0 bg-gradient-to-br from-white to-gray-50 dark:from-gray-800 dark:to-gray-900 shadow-xl">
        <div className="flex items-center justify-between mb-6">
          <div className="flex items-center gap-3">
            <div className="p-3 rounded-xl bg-gradient-to-br from-blue-500 to-purple-600 shadow-lg">
              <Zap className="text-white" size={24} />
            </div>
            <div>
              <h3 className="text-xl font-bold text-gray-900 dark:text-white">Daily Credits</h3>
              <p className="text-sm text-gray-500 dark:text-gray-400">Track your usage</p>
            </div>
          </div>
          <div className="flex items-center gap-2 px-4 py-2 rounded-full bg-blue-100 dark:bg-blue-900/30">
            <Clock size={16} className="text-blue-600 dark:text-blue-400" />
            <span className="text-sm font-semibold text-blue-600 dark:text-blue-400">
              Resets at midnight
            </span>
          </div>
        </div>

        <div className="relative">
          <div className="flex mb-3 items-center justify-between">
            <div className="flex items-center gap-2">
              <span className="text-sm font-semibold text-gray-700 dark:text-gray-300">
                {stats ? stats.usage.count : 0} used
              </span>
              <span className="text-sm text-gray-400">of</span>
              <span className="text-sm font-semibold text-gray-700 dark:text-gray-300">
                {stats ? stats.usage.total : 50} total
              </span>
            </div>
            <div className="text-right">
              <span className={`text-lg font-bold ${isLimitReached
                  ? 'text-red-600 dark:text-red-400'
                  : 'text-blue-600 dark:text-blue-400'
                }`}>
                {usagePercentage}%
              </span>
            </div>
          </div>

          <div className="relative overflow-hidden h-4 rounded-full bg-gray-200 dark:bg-gray-700 shadow-inner">
            <div
              style={{ width: `${usagePercentage}%` }}
              className={`h-full rounded-full transition-all duration-700 ease-out ${isLimitReached
                  ? 'bg-gradient-to-r from-red-500 to-red-600'
                  : 'bg-gradient-to-r from-blue-500 via-purple-500 to-blue-600'
                } shadow-lg`}
            >
              <div className="h-full w-full bg-white/20 animate-pulse"></div>
            </div>
          </div>

          <p className={`text-sm mt-4 flex items-center gap-2 ${isLimitReached
              ? 'text-red-600 dark:text-red-400 font-semibold'
              : 'text-gray-600 dark:text-gray-400'
            }`}>
            {!stats ? 'Loading...' : isLimitReached
              ? "⚠️ You've reached your daily limit. Upgrade your plan or come back tomorrow!"
              : `✨ You can view ${stats.usage.remaining} more contact${stats.usage.remaining !== 1 ? 's' : ''} today.`}
          </p>
        </div>
      </Card>
    </div>
  );
}